import { useState, useEffect, useRef, useMemo } from 'react';
import PropTypes from 'prop-types';
import { CSSTransition, TransitionGroup } from 'react-transition-group';

import useMarvelService from '../../services/MarvelService';
import ErrorMessage from '../errorMessage/errorMessage';
import Spinner from '../spinner/spinner';

import './charList.scss';

const setContent = (process, Component, newItemLoading) => {
    switch (process) {
        case "waiting": 
            return <Spinner/> ;
            break;
        case "loading": 
            return newItemLoading ? <Component/> : <Spinner/> ;
            break;
        case "confirmed": 
            return <Component/>; 
            break;
        case "error": 
            return <ErrorMessage/>;
            break;
        default: 
            throw new Error("Unexpected process state")
    }
  } 

const CharList = (props) => {

    const [charList, setCharList] = useState([]);
    const [newItemLoading, setNewItemLoading]  = useState(false);
    const [offset, setOffset] = useState(210);
    const [charEnded, setCharEnded] = useState(false);

    
    const {loading, error, getAllCharacters, process, setProcess} =  useMarvelService();

    useEffect(() => { // .. useEffect      , 
    //      onRequest     
        onRequest(offset, true);
    }, []) //    -  componentDidMount

    
    const onRequest = (offset, initial) => { //    (1)
        initial ? setNewItemLoading(false) : setNewItemLoading(true); 
        getAllCharacters(offset) //     (4)
            .then(onCharListLoaded) //  onCharListLoaded (5)
            .then(() => setProcess("confirmed")) //    confirmed
    }

    const onCharListLoaded = async (newCharList) => { //      (6)
        let ended = false;
        if (newCharList.length < 9) {
            ended = true;
        }

        //    ..      
        setCharList(charList => [...charList, ...newCharList]); // charList -   , newCharList -  
        //    charList -  
        setNewItemLoading(newItemLoading => false);
        setOffset(offset => offset + 9); //          9 (9  )
        setCharEnded(charEnded => ended)
    }

    //const errorMessage = error ? <ErrorMessage/> : null;
    //const spinner = loading && !newItemLoading ? <Spinner/> : null;
    //const content = !(loading || error) ? <RenderItems arr = {charList} props = {props}/> : null;

    const elements = useMemo (() => {
        return setContent(process, () => <RenderItems arr = {charList} props = {props}/>, newItemLoading)
    }, [process]);

    return (
        <div className="char__list">
            {/* {errorMessage}
            {spinner}
            <RenderItems arr = {charList} props = {props}/> */}
            {
                elements
            }
            <button 
                className="button button__main button__long"
                disabled = {newItemLoading}
                style={{"display": charEnded ? "none" : "block"}} //  ,   
                onClick={() => onRequest(offset)}>
                <div className="inner">load more</div>
            </button>
        </div>
    )
}

//   (    ,    )
const RenderItems = ({arr, props}) => {

    const itemRefs = useRef([]);

    const focusOnItem = (id) => {
        itemRefs.current.forEach(item => item.classList.remove('char__item_selected'));
        itemRefs.current[id].classList.add('char__item_selected');
        itemRefs.current[id].focus();
    }

    const items = arr.map((item, i) => {
        let imgStyle;
        if (item.thumbnail.includes("image_not_available")) {imgStyle = {'objectFit' : 'unset'}}
        
        
        return (
        //    <CSSTransition key={item.id} in={true} timeout={500} classNames="char__item">
                <li className="char__item "
                    key={item.id}
                    tabIndex={0}
                    ref={el => itemRefs.current[i] = el}
                    onClick={(e) => {
                        e.preventDefault();
                        props.onCharSelected(item.id);
                        focusOnItem(i)
                    }}
                    onKeyPress={(e) => {
                        if (e.key === "Enter") {
                            props.onCharSelected(item.id);
                            focusOnItem(i);
                        }
                    }}
                    > 
                    <img style={imgStyle} src={item.thumbnail} alt={item.name} />
                    <div className="char__name">{item.name}</div>
                </li>
            //  </CSSTransition>
        )
    })

    return (
        <ul  className="char__grid">
            {/* <TransitionGroup component={null}> */}
                {items}
            {/* </TransitionGroup> */}
         </ul>

    )
}

CharList.propTypes = {
    onCharSelected: PropTypes.func.isRequired
}


export default CharList;